.using {
  padding-top: 116px;
  @include r(767) {
    padding-top: 70px;
    padding-right: 20px;
    padding-left: 20px; }
  &__title {
    h2 {
      font-size: 36px;
      color: #3c4450;
      font-weight: 500;
      text-align: center;
      @include r(767) {
        font-size: 30px; }
      @include r(425) {
        font-size: 25px; } }
    h3 {
      font-size: 22px;
      color: #707984;
      font-weight: 500;
      text-align: center;
      padding-bottom: 83px;
      @include r(767) {
        padding-bottom: 40px;
        font-size: 19px; }
      @include r(425);
      padding-bottom: 30px {
        font-size: 15px; } } }
  .tabs {
    display: flex;
    @include r(767) {
      display: block; }
    &__caption {
      @include r(767) {
        display: flex;
        justify-content: center; } }
    li.active {
      box-shadow: 0px 11px 21px 0 rgba(20, 19, 23, 0.09);
      background: #ffffff; }
    li {
      margin-top: 10px;
      border-radius: 2px;
      position: relative;
      font-size: 18px;
      color: #3c4450;
      text-align: center;
      padding: 30px 0;
      width: 276px;
      @include r(570) {
        font-size: 12px;
        padding: 20px 0; }
      &:after {
        content: '';
        position: absolute;
        right: -21px;
        top: 32px;
        border: 10px solid transparent;
        border-left: 11px solid white; }
      &:hover {
        box-shadow: 0px 11px 21px 0 rgba(20, 19, 23, 0.09); } }
    .tabs__content {
      display: none;
      padding-left: 128px;
      @include r(767) {
        padding-left: 50px;
        padding-top: 30px; }
      @include r(570) {
        padding-left: 0; }
      .using__block {
        display: flex;
        @include r(767) {
          display: {} }
        strong {
          font-size: 100px;
          font-weight: normal;
          color: #25ce98;
          display: block;
          background-image: linear-gradient(to bottom, #42d79f, #26c8cb), linear-gradient(to bottom, #25ce98, #25ce98);
          -webkit-background-clip: text;
          background-clip: text;
          -webkit-text-fill-color: transparent;
          @include r(767) {
            font-size: 85px; }
          @include r(570) {
            font-size: 70px; } }
        &__text {
          padding-left: 41px;
          @include r(767) {
            padding-left: 25px; }
          @include r(570) {
            padding-left: 15px; }
          p {
            font-size: 18px;
            font-weight: 500;
            color: #707984;
            padding-bottom: 50px;
            padding-top: 15px;
            @include r(850) {
              font-size: 15px; }
            @include r(570) {
              font-size: 13px; } }
          b {
            font-size: 16px;
            font-weight: 700;
            color: #3c4450;
            @include r(850) {
              font-size: 14px; }
            @include r(570) {
              font-size: 12px; } } } } }
    .tabs__content.active {
      display: block; } } }


