.comment {
  padding-top: 120px;
  padding-bottom: 132px;
  @include r(767) {
    padding: 70px 20px; }
  @include r(425) {
    padding: 50px 20px; }
  &__title {
    h2 {
      font-size: 36px;
      font-weight: 500;
      text-align: center;
      color: #3c4450;
      @include r(767) {
        font-size: 30px; }
      @include r(425) {
        font-size: 25px; } }
    h3 {
      font-size: 22px;
      font-weight: 500;
      line-height: 1.82;
      text-align: center;
      color: #707984;
      padding-bottom: 5px;
      @include r(767) {
        font-size: 19px; }
      @include r(425) {
        font-size: 15px; } } }
  .comment__block {
    padding-top: 76px;
    display: flex;
    @include r(767) {
      padding-top: 50px; }
    @include r(425) {
      padding-top: 25px; }
    &__img {
      width: 131px;
      height: 131px;
      border-radius: 100px;
      box-shadow: 0px 11px 21px 0 rgba(20, 19, 23, 0.2);
      border: 8px solid #ffffff;
      background-color: #ffffff;
      overflow: hidden;
      @include r(767) {
        width: 115px;
        height: 115px; }
      @include r(425) {
        width: 110px;
        height: 110px; } }
    &__text {
      padding-left: 20px;
      flex: 1;
      @include r(767) {
        padding-left: 15px; }
      @include r(425) {
        padding-left: 7px; }
      p {
        font-size: 16px;
        font-weight: 500;
        color: #707984;
        padding: 10px 15px 48px 0;
        @include r(767) {
          font-size: 14px;
          padding: 10px 15px 40px 0; }
        @include r(425) {
          font-size: 12px;
          padding: 10px 15px 35px 0; }
        @include r(370) {
          padding: 10px 15px 23px 0; } }
      b {
        font-size: 18px;
        font-weight: 700;
        color: #3c4450;
        @include r(767) {
          font-size: 16px; }
        @include r(425) {
          font-size: 14px; } } } } }
